////
/// This is City banner
////

.banner {
    display: flex;
    flex-direction: column;
    overflow: hidden;
    background-color: $color-banner-this-is-city;
    color: $color-purewhite;

    @include media('>tablet') {
        padding: 0;
        @include standard-grid();
        grid-column: 1/-1;
        grid-template-rows: repeat(3, 1fr);
    }
}

.banner__txt {
    @include media('<=tablet') {
        grid-row: unset;
        grid-column: unset;
        margin: $measure--medium3 0 0 0;
        @include default-padding();
    }
    
    @include media('>tablet') {
        grid-row: 2;
        grid-column: 2 / 12;
    }
}

.banner__ribbon {

    @include media('<=tablet') {
        grid-row: unset;
        grid-column: unset;
        margin: $measure--basic 0 $measure--medium3;
        @include default-padding();
    }

    @include media('>tablet') {
        grid-row: 3;
        grid-column: 2 / 12;
    }
}

.banner__img {
    @include media('<=tablet') {
        display: none;
        grid-column: 12 / -1;
    }

    @include media('>tablet') {
        display: block;
        max-height: $banner-tablet-image-max-height;
        grid-row: 1 / span 3;
        grid-column: 12 / -1;
    }

    @include media('>=desktop') {
        max-height: $banner-desktop-image-max-height;
    }

    @include media('>=4K') {
        max-height: $banner-4k-image-max-height;
    }

    @include media('>=8K') {
        max-height: $banner-8k-image-max-height;
    }

    .hero-image {
        width: 100%;
        overflow: hidden;
        display: flex;

        img {
            width: 100%;
            object-fit: cover;
            align-self: auto;
        }
    }
}

.banner__img--mobile {
    display: block;

    @include media('<=mobile') {
        max-height: $banner-mobile-image-max-height;
    }

    @include media('<=tablet') {
        max-height: $banner-tablet-image-max-height;
    }

    @include media('>tablet') {
        display: none;
    }

    .hero-image {
        width: 100%;
        overflow: hidden;
        display: flex;

        img {
            width: 100%;
            object-fit: cover;
            align-self: auto;
        }
    }
}
