// styles for youtube video collage content template 

.embedded-video--collage__wrapper {
    grid-column: 1/-1;
    padding-bottom: $measure--half;

    .embedded-video--collage__heading {
        @include default-padding();
    }

    .embedded-video--collage__container {    
        @include default-padding();
        display: grid;
        grid-column-gap: $measure--small2;
        grid-row-gap: $measure--small2;
        padding-top: $measure--half;

        &--2-columns {
            grid-template-columns: 1fr 1fr;

            @include media('<=tablet') {
                grid-template-columns: 1fr;
            }
        }

        &--3-columns {
            grid-template-columns: 1fr 1fr 1fr;

            @include media('<=tablet') {
                grid-template-columns: 1fr;
            }
        }

        .embedded-video {
            padding-top: 0;
            padding-bottom: 0;
            
            @include media('<tablet') {
             
            }

            .youtube-video {
                margin-top: 0;
            }
        }
    }
}