////
/// Key selling points (KSPs) block
/// @group patterns
////
.ksp-block {

    @include media('<tablet') {
        .ksp-block__heading,
        .ksp-block__introduction,
        .cta-block {
            padding: 0 $measure--small2;
        }
    }

    @include media('>=tablet') {
        .ksp__items {
            display: flex;
        }
    }

    @include media('>=small') {
        @include standard-grid();
        .ksp-block__heading,
        .ksp-block__introduction,
        .cta-block {
            grid-column: 7/-7;
        }
    }

    // Generic
    background: $color-gray1;
    padding: $measure--basic 0;

    .ksp-block__heading,
    .ksp-block__introduction,
    .cta-block {
        margin-left: auto;
        margin-right: auto;
        text-align: center;
        .cta-block__cta {
            margin-bottom: $measure--basic;
        }
    }

    .ksp__items {
        grid-column: 1/-1;
        margin: $measure--basic 0;
        max-width: none;
        text-align: center;

        .ksp__item {
            list-style-type: none;
            padding: 0 $measure--basic;
        }

        .ksp__item__description {
            margin: $measure--threequarters auto 0;
        }

        .ksp__item__icon {
            width: $measure--medium7;
        }
    }
}