// @mixin cta--basic(
// $color: $color-purewhite,
// $background: $color-red50,
// $hover-color: $color-purewhite,
// $hover-background: $color-red70,
// $outline: $color-purewhite,
// $color-border: $color-red50,
// $color-border-hover: $color-red70,
// $corners: square
// )

/// default / theme-1 CTA on bright background
.cta {
    @include cta--basic(
        $color-purewhite,
        $color-cta--t1,
        $color-purewhite,
        $color-cta-hover--t1,
        $color-nearblack,
        $color-cta--t1,
        $color-cta-hover--t1,
        round
    )
}

.cta--outline {
    @include cta--basic(
        $color-purewhite, // $color
        $color-cta--t1,   // $background
        $color-cta--t1,   // $hover-color
        $color-purewhite, // $hover-background
        $color-nearblack, // $outline
        $color-cta--t1,   // $color-border
        $color-cta--t1,   // $color-border-hover
        round             // corners
    )
}

/// default / theme-1 CTA on dark background
.cta--bg {
    @include cta--basic(
        $color-purewhite,
        $color-cta--t1,
        $color-purewhite,
        $color-cta-hover--t1,
        $color-purewhite,
        $color-cta--t1,
        $color-cta-hover--t1,
        round
    )
}

// theme-2 CTA on bright background
.cta--t2 {
    @include cta--basic(
        $color-purewhite,
        $color-cta--t2,
        $color-purewhite,
        $color-cta-hover--t2,
        $color-nearblack,
        $color-cta--t2,
        $color-cta-hover--t2,
        round
    )
}

// inverse CTA on bright background
.cta--inverse {
    @include cta--basic(
        $color-nearblack,
        transparent,
        $color-purewhite,
        $color-nearblack,
        $color-nearblack,
        $color-nearblack,
        $color-nearblack,
        round
    )
}

// inverse CTA on dark background
.cta--inverse--bg {
    @include cta--basic(
        $color-purewhite,
        transparent,
        $color-nearblack,
        $color-purewhite,
        $color-purewhite, 
        $color-purewhite, 
        $color-purewhite, 
        round
    )
}

.cta--duotone {
    @include cta--basic(
        $color-nearblack,
        $color-purewhite,
        $color-purewhite,
        $color-cta--t1,
        $color-nearblack,
        $color-cta--t1,
        $color-cta--t1,
        round            
    )
}

// cta-icon--arrow cannot be used on its own, 
// it only adds 'icon float-in' functionalyty which means
// it has to be used together with with a cta class; by default the icon is an arrow
// i.e class="cta cta-icon--arrow"
.cta-icon--arrow {
    @include cta--icon();
}
