////
/// Pathway cards block
/// @group patterns
////
.pathway {

    .pathway__heading {
        margin-top: 0;
        padding-left: $measure--half;
        padding-right: $measure--half;
    }

    .pathway__cards {
        @include media('>=tablet') {
            display: grid;
            grid-template-columns: repeat(3, 1fr);
            grid-column-gap: $measure--small2;
        }

        list-style: none;
        margin: 0;
        max-width: unset;
        padding-bottom: $measure--small1;

        .card--pathway {
            margin-top: $measure--basic;

            a {
                @include pathway-card();
                &.card--pathway__content--border {
                    border-top: $measure--half solid $color-red50;
                }
            }

            .card--pathway__content {
                @include media('>=mobile', '<tablet') {
                    display: flex;
                    picture {
                        max-width: $measure--huge5;
                    }
                }
            }

            .card--pathway__content__text {
                padding: 0 $measure--basic $measure--basic $measure--basic;
            }

            .card--pathway__content__heading {
                @include betatype();
                .icon {
                    margin-left: $measure--quarter;
                }
            }

        }
    }
}