main {
    .youtube-description-cta {

        @include media('>=small') {
            .youtube-video {
                margin-top: $measure--threequarters;
            }
        }

        @include media('>=tablet') {
            display: flex;
            grid-column: 2/-2;
            .youtube-description-cta__heading {
                text-align: center;
            }
            .youtube-description-cta__panel {
                flex: 1;
                &.youtube-description-cta__panel__text {
                    margin-right: $measure--small2;
                }
            }
        }
    
        @include media('<tablet') {
            grid-column: 1/-1;
            @include default-padding;
            .youtube-description-cta__panel__video {
                margin-top: $measure--small2; 
            }
        }
    
        // Generic
        margin: 0 0 $measure--basic 0;
        flex-wrap: wrap;
        .youtube-description-cta__heading {
            flex-basis: 100%;
        }
    }
}